<template>
  <div class="flex h-screen">
    <div class="m-auto">
      <div :class="[ change ? 'grid grid-cols-2 gap-12':'grid']">
        <div>
          <div>
            <LineComponent :val='rootIcon[5]' />
            <LineComponent :val='rootIcon[4]' />
            <LineComponent :val='rootIcon[3]' />
            <LineComponent :val='rootIcon[2]' />
            <LineComponent :val='rootIcon[1]' />
            <LineComponent :val='rootIcon[0]' />
          </div>
          <div class="mt-4 text-center font-bold">{{ rootHex.num }}</div>
        </div>
        <div v-if="change">
          <div>
            <LineComponent :val='swayIcon[5]' />
            <LineComponent :val='swayIcon[4]' />
            <LineComponent :val='swayIcon[3]' />
            <LineComponent :val='swayIcon[2]' />
            <LineComponent :val='swayIcon[1]' />
            <LineComponent :val='swayIcon[0]' />
          </div>
          <div class="mt-4 text-center font-bold">{{ swayHex.num }}</div>
        </div>
      </div>
      <div class="mt-6 text-center">
        <button 
          @click="cast" 
          class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-3 px-3 rounded outline-none"
          >
          <svg v-if="!rootHex" class="fill-current w-8 h-8" xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0zm13.97 21.49c-.63.23-1.29.4-1.97.51.68-.12 1.33-.29 1.97-.51zM12 22c-.68-.12-1.33-.29-1.97-.51.64.22 1.29.39 1.97.51z" fill="none"/><path d="M8.55 12c-1.07-.71-2.25-1.27-3.53-1.61 1.28.34 2.46.9 3.53 1.61zm10.43-1.61c-1.29.34-2.49.91-3.57 1.64 1.08-.73 2.28-1.3 3.57-1.64z"/><path d="M15.49 9.63c-.18-2.79-1.31-5.51-3.43-7.63-2.14 2.14-3.32 4.86-3.55 7.63 1.28.68 2.46 1.56 3.49 2.63 1.03-1.06 2.21-1.94 3.49-2.63zm-6.5 2.65c-.14-.1-.3-.19-.45-.29.15.11.31.19.45.29zm6.42-.25c-.13.09-.27.16-.4.26.13-.1.27-.17.4-.26zM12 15.45C9.85 12.17 6.18 10 2 10c0 5.32 3.36 9.82 8.03 11.49.63.23 1.29.4 1.97.51.68-.12 1.33-.29 1.97-.51C18.64 19.82 22 15.32 22 10c-4.18 0-7.85 2.17-10 5.45z"/></svg>
          <svg v-else class="fill-current w-8 h-8" xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0z" fill="none"/><path d="M17.65 6.35C16.2 4.9 14.21 4 12 4c-4.42 0-7.99 3.58-7.99 8s3.57 8 7.99 8c3.73 0 6.84-2.55 7.73-6h-2.08c-.82 2.33-3.04 4-5.65 4-3.31 0-6-2.69-6-6s2.69-6 6-6c1.66 0 3.14.69 4.22 1.78L13 11h7V4l-2.35 2.35z"/></svg>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import LineComponent from './components/Line'

export default {
  name: 'App',
  components: {
    LineComponent
  },
  data () {
    return {
      iching: '',
      pile: '',
      east: '',
      west: '',
      rootIcon: '',
      swayIcon: '',
      change: true,
      rootHex: '',
      swayHex: ''
    }
  },
  methods: {
    divideStalks (yarrowStalks) {
      var westPile = Math.floor((Math.random() * yarrowStalks) + 1)
      var eastPile = yarrowStalks - westPile
      westPile = westPile - 1
      
      this.east = (eastPile % 4)
      this.west = (westPile % 4)

      if (this.east === 0) { this.east = 4 }
      if (this.west === 0) { this.west = 4 }

      this.pile = this.east + this.west + 1
    },
    lineCast () {
      var stalks = 49

      var countValueOne = 0
      var countValueTwo = 0
      var countValueThree = 0

      this.divideStalks(stalks)

      if (this.east + this.west + 1 === 9) { countValueOne = 2 }
      if (this.east + this.west + 1 === 5) { countValueOne = 3 }
      
      stalks = stalks - this.pile

      this.divideStalks(stalks)

      if (this.east + this.west + 1 === 8) { countValueTwo = 2 }
      if (this.east + this.west + 1 === 4) { countValueTwo = 3 }
      
      stalks = stalks - this.pile

      this.divideStalks(stalks)

      if (this.east + this.west + 1 === 8) { countValueThree = 2 }
      if (this.east + this.west + 1 === 4) { countValueThree = 3 }
      
      var lineValue = countValueOne + countValueTwo + countValueThree

      return lineValue
    },
    getAnswer () {
      let arr = []
      for (let i = 0; i < 6; i++) { arr.push(this.lineCast()) }
      
      let iconOpts = { 6: 7, 7: 7, 8: 8, 9: 8 }
      this.rootIcon = arr.join('')
      this.swayIcon = this.rootIcon.replace(/[6789]/g, m => iconOpts[m])


      if (this.rootIcon === this.swayIcon) {
        this.change = false
      } else {
        this.change = true
      }

      let rootOpts = { 6: 0, 7: 1, 8: 0, 9: 1 }
      this.rootHex = this.rootIcon.replace(/[6789]/g, m => rootOpts[m])
      this.rootHex = this.iching.find(hex => hex.code === this.rootHex.toString())

      let swayOpts = { 6: 0, 7: 1, 8: 0, 9: 1 }
      this.swayHex = this.swayIcon.replace(/[6789]/g, m => swayOpts[m])
      this.swayHex = this.iching.find(hex => hex.code === this.swayHex.toString())
    },
    cast () {
      this.getAnswer()
    }
  },
  mounted () {
    axios.get('/data/iching.json')
      .then(response => {
        this.iching = response.data
      })
      .catch(error => console.log(error))
  }  
}
</script>

